/* SigninPage.module.css */

.container {
  border: 1px solid;
  width: 100%;
}

/* Mobile and <*/
@media screen and (max-width: 767px) {
  .container {
    /* background-image: url('../../images/mobile-background-element-sign-in.svg'),
      url('../../images/mobile-bottle-sign-in.svg'); */
    background-repeat: no-repeat, no-repeat;
    background-position: center, bottom;
  }
}

/* Tablet and > */
@media screen and (min-width: 768px) {
  .container {
    /* background-image: url('../../images/mobile-background-element-sign-in.svg'),
      url('../../images/tablet-bottle-sign-in.svg'); */
    background-repeat: no-repeat, no-repeat;
    background-position: top, right;
  }
}

/* Desktop and > */
@media screen and (min-width: 1440px) {
  .container {
    /* background-image: url('../../images/mobile-background-element-sign-in.svg'),
      url('../../images/desktop-bottle-sign-in.svg'); */
    background-repeat: repeat, no-repeat;
    background-position: center center, left;
  }
}

.mainstr {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 100vh;
}

/* Tablet and > */
@media screen and (min-width: 768px) {
  .mainstr {
    justify-content: flex-start;
    align-items: flex-start;
  }
}

/* Desktop and > */
@media screen and (min-width: 1440px) {

  .mainstr {
    justify-content: flex-end;
    align-items: center;
  }

}

.form {
  margin-top: 24px;
}

/* Tablet and > */
@media screen and (min-width: 768px) {
  .form {
    margin-top: 40px;
    margin-left: 32px;
  }
}

/* Desktop and > */
@media screen and (min-width: 1440px) {
  .form {

    margin-right: 198px;
  }

}

.formControl {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  /* margin: 0 auto; */
  padding: 10px;
  gap: 16px;
}

.formTitle {
  padding-left: 10px;
  font-size: 26px;
}

.formLabel {
  margin-bottom: 8px;
}

.stack {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.input {
  padding: 12px 10px;
  width: 280px;
  border: 2px solid var(--secondary-color-blue-3);
  border-radius: 6px;
  outline: none;
}

/* Tablet and > */
@media screen and (min-width: 768px) {
  .input {
    width: 336px;
  }
}

/* Desktop and > */
@media screen and (min-width: 1440px) {
  .input {
    width: 384px;
  }
}

.inputBox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 6px 10px;
  width: 280px;
  border: 2px solid var(--secondary-color-blue-3);
  border-radius: 6px;
  outline: none;
}

/* Tablet and > */
@media screen and (min-width: 768px) {
  .inputBox {
    width: 336px;
  }
}

/* Desktop and > */
@media screen and (min-width: 1440px) {
  .inputBox {
    width: 384px;
  }
}

.iconeye {
  stroke: var(--primary-color-blue);
}

.inputpassword {

  outline: none;
  border: none;
  padding: 0;
  width: 280px;
}

/* Tablet and > */
@media screen and (min-width: 768px) {
  .inputpassword {
    width: 336px;
  }
}

/* Desktop and > */
@media screen and (min-width: 1440px) {
  .inputpassword {
    width: 384px;
  }

}

.input::placeholder {
  color: var(--secondary-color-blue-3);
}

.inputpassword::placeholder {
  color: var(--secondary-color-blue-3);
}

.button {
  background-color: var(--primary-color-blue);
  color: var(--primary-color-white);
  padding: 10px 30px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  width: 280px;
  height: 36px;
}



/* Tablet and > */
@media screen and (min-width: 768px) {
  .button {
    width: 336px;
    height: 44px;
  }
}

/* Desktop and > */
@media screen and (min-width: 1440px) {
  .button {
    width: 384px;
  }
}



.googleLogin {
  background-color: var(--primary-color-white);
  color: var(--primary-color-blue);
  padding: 5px 10px;
  border-radius: 6px;
  cursor: pointer;
  width: 280px;
  height: 34px;
  text-decoration-line: none;
  text-align: center;
  box-shadow: 0 6px 8px rgba(54, 47, 192, 0.3), 0 2px 6px rgba(0, 0, 0, 0.08);
}



/* Tablet and > */
@media screen and (min-width: 768px) {
  .googleLogin {
    width: 336px;

  }
}

/* Desktop and > */
@media screen and (min-width: 1440px) {
  .googleLogin {
    width: 384px;
  }
}


.errormessage {

  color: var(--secondary-color-red);
  font-size: 14px;
}

.errorBorder {
  border: 2px solid var(--secondary-color-red);
}


.errorInput {
  color: var(--secondary-color-red);
}

.errorInput::placeholder {
  color: var(--secondary-color-red);
}

.signup {
  color: var(--primary-color-blue);
  text-decoration-line: none;
  display: block;
  margin-right: auto;
}


.forgotPassword {
  color: var(--primary-color-blue);
  text-decoration-line: none;
  display: block;
  margin-left: auto;
}

.wraplink {
  display: flex;
  justify-content: space-between;
  align-items: center;
}